<template>
    <div class="container">
        <div class="row justify-content-center">
            <div class="col">
                <div class="card">
                    <div class="card-header text-center">
                        <div class="row justify-content-center">
                            <div class="col-auto"></div>
                            <div class="col-8 h3" >{{user.name}} </div>
                        </div>
                    </div>

                    <div class="card-body">
                        <div class="row">
                            <div class="col">
                                <div></div>
                            </div>
                        </div>
                        <div class="row mt-3 mb-1 align-items-center">
                            <div class="col-2 text-right"> : </div>
                            <div class="col-auto"> {{user.nickname}}</div>
                        </div>
                        <div class="row mt-1 mb-1">
                            <div class="col-2 text-right"> : </div>
                            <div class="col-auto">{{userAge}}</div>
                        </div>
                        <div class="row mt-1 mb-3 align-items-center">
                            <div class="col-2 text-right"> : </div>
                            <div class="col-auto">
                                <div>{{user.birthday}}</div>
                            </div>
                        </div>
                    </div>                   
                </div>
            </div>
        </div>
        <div class="row mt-3">
            <div class="col" v-if="user.is_friend">
                <div class="btn btn-primary" @click="onMessage()"></div>
            </div>
            <div class="col" v-else>
                <div class="btn btn-primary" @click="onFriendApply()"></div>
            </div>
        </div>
    </div>
</template>

<script>
import moment from "moment";
export default {
    props: ['user_id'],
    data () {
        return {
            user: {
                id: null,
                name: null,
                nickname: null,
                birthday: null,
                is_friend: false,
            },
            isLoading: false,
        }
    },
    computed: {
        userAge () {
            if (!this.user.birthday) return ''
            let birthday = moment(this.user.birthday)
            return moment().diff(birthday, 'years')
        }
    },
    mounted () {
        this.getItems()
    },
    methods: {
            getItems () {
            this.isLoading = true
            axios.get('/api/friend/' + this.user_id)
            .then(res => {
                this.user = res.data
            }).catch(error => {
                alert(error)
            }).finally(resp => {
                this.isLoading = false
            })
        },
        onFriendApply() {
            if(confirm('')) {
                axios.post('/api/friend/add/', {
                    friend_id: this.user_id,
                }).then(res => {
                    if (res.data.message.length) {
                        alert(res.data.message)
                    } else {
                        alert('')
                        this.getItems()
                    }
                }).catch(error => {
                    alert('')
                })
            }
        },
        onMessage: function () {
            this.$router.push({ name: 'chat.private', params: { 'user_id': this.user.id} })
        },
    }
}
</script>
